<template>
  <el-container>
    <el-main style="padding:0px">
      <el-form ref="forms" :label-position="labelPosition" :model="formData" :label-width="labelWidth+'px'">
        <template v-for="(item,index) of columns">
          <el-col v-if="item.effect" :key="index" :style="item.effect&&item.effect === 'form'?'padding-bottom: 20px':'padding-bottom: 21px'" :xs="24" :sm="grid(item,12)" :md="grid(item,12)" :lg="grid(item,8)">
            <template v-if="item.effect&&item.effect == 'form'?true:false">
              <!-- 下拉框 -->
              <el-form-item v-if="item.type==='select'" :label="item.label" :prop="item.pram" :rules="rules(item)" class="formitem">
                <span slot="label">
                  <slot :row="item.label" :name="item.pram+'title'">
                    {{ item.label }}
                  </slot>
                </span>
                <slot :row="formData[item.pram]" :name="item.pram+'Content'">
                  <el-select v-model="formData[item.pram]" :filterable="translate(item.filterable)" :clearable="translate(item.clearable)" :multiple="item.multiple" collapse-tags :placeholder="item.placeholder?item.placeholder:'请选择'" :style="{width:valueWidth+'%'}" @change="itemChange(item.pram,formData[item.pram])">
                    <el-option v-for="(select,i) in item.data" :key="i" :label="label(item,select)" :value="value(item,select)" />
                  </el-select>
                </slot>
              </el-form-item>
              <!-- 级联选择器 -->
              <el-form-item v-else-if="item.type==='cascader'" :label="item.label" :prop="item.pram" :rules="rules(item)" class="formitem">
                <span slot="label">
                  <slot :row="item.label" :name="item.pram+'title'">
                    {{ item.label }}
                  </slot>
                </span>
                <slot :row="formData[item.pram]" :name="item.pram+'Content'">
                  <el-cascader
                    v-model="formData[item.pram]"
                    :options="item.data"
                    :filterable="translate(item.filterable)"
                    :clearable="translate(item.clearable)"
                    :props="item.props?item.props:{ value: 'value', label: 'label' }"
                    collapse-tags
                    :show-all-levels="translate(item.showAllLevels)"
                    :placeholder="item.placeholder?item.placeholder:'请选择'"
                    :style="{width:valueWidth+'%'}"
                    :disabled="item.disabled"
                    @change="itemChange(item.pram,formData[item.pram])"
                  />
                </slot>
              </el-form-item>
              <!-- 时间选择器 -->
              <el-form-item v-else-if="item.type==='date'" :label="item.label+':'" :prop="item.pram" :rules="rules(item)" class="formitem">
                <span slot="label">
                  <slot :row="item.label" :name="item.pram+'title'">
                    {{ item.label }}
                  </slot>
                </span>
                <slot :row="formData[item.pram]" :name="item.pram+'Content'">
                  <el-date-picker v-model="formData[item.pram]" start-placeholder="开始日期" end-placeholder="结束日期" :type="item.dateType?item.dateType:'date'" :format="item.format?item.format:'yyyy-MM-dd'" :value-format="item.format?item.format:'yyyy-MM-dd'" :placeholder="item.placeholder?item.placeholder:'请选择日期'" :style="{width:valueWidth+'%'}" @change="itemChange(item.pram,formData[item.pram])" />
                </slot>
              </el-form-item>
              <!-- 复选框 -->
              <el-form-item v-else-if="item.type==='checkbox'" :label="item.label" :prop="item.pram" :rules="rules(item)" class="formitem">
                <span slot="label">
                  <slot :row="item.label" :name="item.pram+'title'">
                    {{ item.label }}
                  </slot>
                </span>
                <slot :row="formData[item.pram]" :name="item.pram+'Content'">
                  <el-checkbox-group v-model="formData[item.pram]" :style="{width:valueWidth+'%'}" @change="itemChange(item.pram,formData[item.pram])">
                    <el-checkbox v-for="(select,i) in item.data" :key="i" :label="value(item,select)" :disabled="eval(select,item.disabled)">{{ label(item,select) }}</el-checkbox>
                  </el-checkbox-group>
                </slot>
              </el-form-item>
              <!-- 单选框 -->
              <el-form-item v-else-if="item.type==='radio'" :label="item.label" :prop="item.pram" :rules="rules(item)" class="formitem">
                <span slot="label">
                  <slot :row="item.label" :name="item.pram+'title'">
                    {{ item.label }}
                  </slot>
                </span>
                <slot :row="formData[item.pram]" :name="item.pram+'Content'">
                  <el-radio-group v-model="formData[item.pram]" :style="{width:valueWidth+'%'}" @change="itemChange(item.pram,formData[item.pram])">
                    <el-radio v-for="(select,i) in item.data" :key="i" :label="value(item,select)" :disabled="eval(select,item.disabled)">{{ label(item,select) }}</el-radio>
                  </el-radio-group>
                </slot>
              </el-form-item>
              <!-- 文本框 -->
              <el-form-item v-else-if="item.type==='textarea'" :label="item.label" :prop="item.pram" :rules="rules(item)" class="formitem">
                <span slot="label">
                  <slot :row="item.label" :name="item.pram+'title'">
                    {{ item.label }}
                  </slot>
                </span>
                <slot :row="formData[item.pram]" :name="item.pram+'Content'">
                  <el-input
                    v-if="item.maxlength"
                    v-model="formData[item.pram]"
                    style="min-height: 37.33px;"
                    type="textarea"
                    :maxlength="item.maxlength"
                    show-word-limit
                    :autosize="item.autosize?item.autosize: { minRows: 1, maxRows: 1}"
                    :clearable="translate(item.clearable)"
                    :disabled="item.disabled"
                    :style="{width:valueWidth+'%'}"
                    :placeholder="item.placeholder?item.placeholder:'请输入'"
                    @blur="itemChange(item.pram,formData[item.pram])"
                  />
                  <el-input v-else v-model="formData[item.pram]" type="textarea" style="min-height: 37.33px;" :autosize="item.autosize?item.autosize: { minRows: 1, maxRows: 1}" :clearable="translate(item.clearable)" :disabled="item.disabled" :style="{width:valueWidth+'%'}" :placeholder="item.placeholder?item.placeholder:'请输入'" @blur="itemChange(item.pram,formData[item.pram])" />
                </slot>
              </el-form-item>
              <!-- 密码框 -->
              <el-form-item v-else-if="item.type==='possword'" :label="item.label" :prop="item.pram" :rules="rules(item)" class="formitem">
                <span slot="label">
                  <slot :row="item.label" :name="item.pram+'title'">
                    {{ item.label }}
                  </slot>
                </span>
                <slot :row="formData[item.pram]" :name="item.pram+'Content'">
                  <el-input v-model="formData[item.pram]" :placeholder="item.placeholder?item.placeholder:'请输入密码'" show-password :style="{width:valueWidth+'%'}" @blur="itemChange(item.pram,formData[item.pram])" />
                </slot>
              </el-form-item>
              <!-- 带放大镜的搜索框 用来打开弹窗 -->
              <el-form-item v-else-if="item.type==='choice'" :label="item.label" :prop="item.pram" :rules="rules(item)" class="formitem">
                <span slot="label">
                  <slot :row="item.label" :name="item.pram+'title'">
                    {{ item.label }}
                  </slot>
                </span>
                <slot :row="formData[item.pram]" :name="item.pram+'Content'">
                  <el-input v-model="formData[item.pram]" class="choice" readonly :placeholder="item.placeholder?item.placeholder:'请选择'+item.label" :suffix-icon="item.suffixIcon?item.suffixIcon:'el-icon-search'" :style="{width:valueWidth+'%'}" @click.native="choice(item.pram,formData[item.pram])" />
                </slot>
              </el-form-item>
              <el-form-item v-else :label="item.label" :prop="item.pram" :rules="rules(item)" class="formitem">
                <span slot="label">
                  <slot :row="item.label" :name="item.pram+'title'">
                    {{ item.label }}
                  </slot>
                </span>
                <slot :row="formData[item.pram]" :name="item.pram+'Content'">
                  <el-input v-model="formData[item.pram]" :clearable="translate(item.clearable)" :disabled="item.disabled" :style="{width:valueWidth+'%'}" :placeholder="item.placeholder?item.placeholder:'请输入'" @blur="itemChange(item.pram,formData[item.pram])" />
                </slot>
              </el-form-item>
            </template>
            <template v-else-if="item.effect&&item.effect == 'exhibit'?true:false">
              <!-- 如果是按钮 -->
              <div v-if="item.type==='btn'" class="formitem flex" style="width:100%">
                <slot :row="item.label" :name="item.pram+'title'">
                  <div class="formtitle" :style="{width:labelWidth+'px','min-width': labelWidth+'px'}">
                    {{ item.label }}：
                  </div>
                </slot>
                <slot :row="formData" :name="item.pram+'Content'">
                  <div class="formcontent" :style="{width:valueWidth+'%'}">
                    <el-link :underline="false" type="primary" @click="detailButton(item.btnType,formData)">
                      {{ item.pram }}
                    </el-link>
                  </div>
                </slot>
              </div>
              <!--  使用过滤器进行展示数据 -->
              <div v-else-if="item.funName" class="formitem flex" style="width:100%">
                <slot :row="item.label" :name="item.pram+'title'">
                  <div class="formtitle" :style="{width:labelWidth+'px','min-width': labelWidth+'px'}">
                    {{ item.label }}：
                  </div>
                </slot>
                <slot :row="formData" :name="item.pram+'Content'">
                  <div class="formcontent" :style="{width:valueWidth+'%'}">
                    {{ getFileter(item.funName,formData[item.pram] ) }}
                  </div>
                </slot>
              </div>
              <!-- 拼接展示 -->
              <div v-else-if="item.type==='splice'" class="formitem flex" style="width:100%">
                <slot :row="item.label" :name="item.pram+'title'">
                  <div class="formtitle" :style="{width:labelWidth+'px','min-width': labelWidth+'px'}">
                    {{ item.label }}：
                  </div>
                </slot>
                <slot :row="formData" :name="item.pram+'Content'">
                  <div v-if="item.pram!==''&&item.pram!=='undefined'" class="textAlong formcontent" style="display:flex" :style="{width:valueWidth+'%'}">
                    <div v-for="(spac,z) in (item.pram).split(item.spliceType&&item.spliceType.length>0?(item.spliceType.split(','))[0]:'/')" :key="z" style="margin-right:5px">
                      <span v-if="item.spliceType&&item.spliceType.length>2&&z>0">{{ item.spliceType.substring(2) }}</span>
                      <span v-if="item.spliceType&&item.spliceType.length<=2&&z>0">/</span>
                      <span v-if="!item.spliceType&&z>0">/</span>
                      {{ formData[spac] }}
                    </div>
                  </div>
                </slot>
              </div>
              <!-- 展示图片 -->
              <div v-else-if="item.type==='img'" class="formitem flex" style="width:100%">
                <slot :row="item.label" :name="item.pram+'title'">
                  <div class="formtitle" :style="{width:labelWidth+'px','min-width': labelWidth+'px'}">
                    {{ item.label }}：
                  </div>
                </slot>
                <slot :row="formData" :name="item.pram+'Content'">
                  <div>
                    <el-image v-if="!(formData[item.pram])" :src="nopicture" style="width:60px;height:40px" />
                    <el-badge v-if="formData[item.pram]&&Array.isArray(formData[item.pram]) && formData[item.pram].length > 1" class="item" style="overflow: hidden;">
                      <span v-for="(img,i) in !(item.limit)&&formData[item.pram].length<3?formData[item.pram].length:!(item.limit)&&formData[item.pram].length>=3?3:item.limit&&formData[item.pram].length<item.limit?formData[item.pram].length:item.limit&&formData[item.pram].length>=item.limit?item.limit:0" :key="i">
                        <el-image :src="formData[item.pram][i]" :preview-src-list="formData[item.pram]" style="width:60px;height:40px;margin-right:8px" />
                      </span>
                    </el-badge>
                    <el-image v-if="formData[item.pram]&&!(Array.isArray(formData[item.pram]))" :preview-src-list="[formData[item.pram]]" :src="formData[item.pram]" style="width:60px;height:40px" />
                  </div>
                </slot>
              </div>
              <div v-else class="formitem flex">
                <slot :row="item.label" :name="item.pram+'title'">
                  <div class="formtitle" :style="{width:labelWidth+'px','min-width': labelWidth+'px'}"> {{ item.label }}： </div>
                </slot>
                <slot :row="formData[item.pram]" :name="item.pram+'Content'">
                  <div class="formcontent" :style="{width:valueWidth+'%'}">
                    {{ formData[item.pram] === "null" ? "": formData[item.pram] }}
                  </div>
                </slot>
              </div>
            </template>
          </el-col>
        </template>
      </el-form>
    </el-main>
    <el-footer v-if="operation.length>0" style="text-align:center;line-height: 50px;height: 100px;">
      <slot :row="formData" name="footerBtn">
        <el-button v-for="(item,i) in operation" :key="i" :type="item.primary?item.primary:'primary'" round @click="submitForm(item.btnType,formData)">
          {{ item.label }}
        </el-button>
      </slot>
    </el-footer>
  </el-container>
</template>
<script>
/**
 * 	 * list 详情页面
   * @description 编辑详情组件，主要用于编辑和展示
   * @property {Array} mainData form表单数据
   * @property {Array} columns form表单title及绑定数据参数等
   * @property {String} labelPosition  label展示的位置
   * @property {Number} labelWidth  label宽度  单位px
   * @property {Number} valueWidth  内容宽度  单位%
   * @property {String} effect // 产生效果 若只包含展示或者表单一种情况可选值[form(form表单)|exhibit(页面展示)]
   * form表单类
   * @property {String} columns[i].pram   绑定展示数据属性名
   * @property {String} columns[i].label  表单标题
   * @property {Object} columns[i].rules  form表单验证
   * @property {Array} columns[i].data    下拉框  多选框   单选框等数据
   * @property {String} columns[i].type   表单类型 可选值[select| date|checkbox|radio|textarea|possword|]
   * @property {Boolean} columns[i].disabled  是否禁用
   * @property {Array} operation          表单操作按钮
   * @property {String} operation[i].label   按钮名称
   * @property {String} operation[i].btnType   按钮类型 可选[reset(重置)|其他自定义类型]
   * @property {String} operation[i].primary   按钮颜色
   * @property {Boolean} operation[i].multiple   下拉框多选
例：columns: [{ pram: 'code', label: '物料编号',effect: 'form',},//正常input输入框
      { pram: 'complianceRate', label: '开关',effect: 'form', type: "select", data: [{ label: '汉堡', value: '1' }, { label: '汉堡1', value: '2' }] },//下拉框单选 data必须保持label  value格式数据
      { pram: 'complianceRate', label: '开关',effect: 'form', type: "select", multiple: true,  data: [{ label: '汉堡', value: '1' }, { label: '汉堡1', value: '2' }] },//下拉框 多选 data必须保持label  value格式数据
      { pram: 'name', label: '物料名称', effect: 'form',type: "date", dateType: 'month', format: "yyyy-MM-dd HH:mm:ss" },//日期 dateType  format可选参数   dateType对应element的type属性
      { pram: 'type', label: '规格型号', effect: 'form',type: "checkbox", data: [{ label: '汉堡', value: '1' }, { label: '汉堡1', value: '2' }] }//多选框 data必须保持label  value格式数据
      { pram: 'materialgrade', label: '物料等级',effect: 'form', type: "radio", data: [{ label: '汉堡', value: '1' }, { label: '汉堡1', value: '2' }] },//单选框 data必须保持label  value格式数据
      { pram: 'quantitystock', label: '库存总量', effect: 'form',type: "possword", },//密码框
      { pram: 'stockline', label: '库存警戒线中间',effect: 'form', disabled: true },//禁用模式
      ]
例：rules: { required: true, tips: '地址', }//正常必填项 required是否必填，tip提示词(不填默认提示label值)
    rules: { required: true, tips: '地址', type: 'num' }输入框验证 type可选值[num(数字)|email(邮箱)|mobile(手机号)|chinese(汉字类型)|money(金额保留两位小数)|blank(不可输入空格数据)]
    rules: { tips: '地址', select: 'true' }//选择验证
   * 详情展示类
   * @property {String} columns[i].pram   绑定展示数据属性名
   * @property {String} columns[i].label  详情标题
   * @property {Boolean} columns[i].type  表单类型 可选值[btn(使用按钮)|splice(使用拼接)|]
   * @property {String} columns[i].btnType 按钮类型
   * @property {String} columns[i].funName  使用过滤器 并且过滤器的名称为columns[i].funName的值
   * @property {String} columns[i].spliceType  拼接规则  默认pram使用绑定的属性名/拼接，展示数据也是/拼接。特殊：spliceType为一个分割字符串"pram通过什么分割的,页面展示同什么符号分割"
例：columns: [{pram: 'conId',label: '合同编号'},//正常数据
      { pram: '按钮名称', label: '操作', type: 'btn', btnType: "edit", effect: 'exhibit' }, // 按钮
      { pram: 'nature', label: '生产性质', funName: "natureFilter", effect: 'exhibit' }, // 过滤展示
      { pram: 'specs/making', label: '规格/型号', type: 'splice', spliceType: "/,、", effect: 'exhibit' }, // 拼接展示 spliceType表示通过/截取pram  使用、拼接展示
      ]
   * @event {Function} detailButton(btnType, row) 点击展示模块按钮触发 btnType为按钮类型，row为详情数据
   * @event {Function} submitForm(btnType, row) 点击operation中按钮触发 btnType为按钮类型，row为详情数据 type=reset时为重置方法
   * @event {Function} itemChange(pram, value) 监听form表单item数据改变 pram为当前item绑定参数，value为当前item值
   *
   */
export default {
  name: 'TableElement',
  props: {
    labelPosition: { // label展示的位置
      type: String,
      default: function() {
        return 'right'
      }
    },
    labelWidth: { // label宽度  单位px
      type: Number,
      default: function() {
        return 140
      }
    },
    valueWidth: { // 内容宽度  单位%
      type: Number,
      default: function() {
        return 80
      }
    },
    formData: {
      type: Object,
      default: function() {
        return {}
      }
    },
    columns: {
      type: Array,
      default: function() {
        return []
      }
    },
    operation: {
      type: Array,
      default: function() {
        return []
      }
    },
  },
  data() {
    return {
      checkList: [],
      tempFrom: {},
      timeouts: "",
      nopicture: require('@/assets/image/nopicture.png'),
    }
  },
  watch: { // 当数据发生改变时，会自动去计算

  },
  created() {
    this.columns.forEach(element => {
      if (this.formData[element.pram] === undefined) {
        if (element.type === 'checkbox') {
          this.$set(this.formData, element.pram, [])
        } else if (element.type === 'select' && element.multiple) {
          this.$set(this.formData, element.pram, [])
        } else if (element.type === 'btn') {
          console.log()
        } else {
          this.$set(this.formData, element.pram, '')
        }
      }
    });
    this.tempFrom = JSON.parse(JSON.stringify(this.formData))
  },
  mounted() { },
  methods: {
    submitForm(type, row) {
      let pass = true
      if (type === 'reset') { // 重置
        this.columns.forEach(element => {
          if (this.formData[element.pram]) {
            if (element.type === 'checkbox') {
              this.$set(this.formData, element.pram, [])
            } else if (element.type === 'select' && element.multiple) {
              this.$set(this.formData, element.pram, [])
            } else if (element.type === 'btn') {
              console.log()
            } else {
              this.$set(this.formData, element.pram, '')
            }
          }
        });
      } else {
        this.$refs['forms'].validate((valid) => {
          if (valid) {
            this.$emit('submitForm', type, this.formData)
            pass = true
          } else {
            pass = false
            console.log('error submit!!');
            return false;
          }
        });
      }
      return pass
    },
    eval(row, condition) {
      if (row) {
        var pattern = new RegExp("[<>=+-]");
        // eslint-disable-next-line no-eval
        return eval(pattern.test(condition) ? condition : JSON.stringify(condition))
      }
    },
    buildImg(btnType, row) {
      this.$emit('buildImg', btnType, row)
    },
    detailButton(btnType, row) {
      this.$emit('detailButton', btnType, row)
    },
    choice(pram, value) {
      this.$emit('choice', pram, value)
    },
    itemChange(pram, value) {
      this.$emit('itemChange', pram, value)
    },
    getFileter(funName, code) {
      return this.$options.filters[funName](code)
    },
    setDetailMsg(pram, value) {
      this.formData[pram] = value
    },
    // 页面加载方法
    rules(item) { // 表单验证规则
      const stack = {
        ...item.rules,
        tips: item.rules && item.rules.tips ? item.rules && item.rules.tips : item.label,
        value: this.formData[item.pram]
      }
      return this.$validate.filter_rules(stack)
    },
    translate(value) { // 转译，传参为true或者不传都为true 传参为false时为false
      return !(JSON.stringify(value) === 'false')
    },
    label(item, select) { // props label取值
      return item.props && item.props.label ? select[item.props.label] : select.labelitem.props && item.props.label ? select[item.props.label] : select.label
    },
    value(item, select) { // props value取值
      return item.props && item.props.value ? select[item.props.value] : select.value
    },
    grid(item, num) { // 判断栅格数量 以传参最大为准
      return item.proport && item.proport > num ? parseInt(item.proport) : num
    },
  }
}
</script>

<style lang="scss" scoped>
.formElcol {
  margin-bottom: 20px;
}
.flex {
  display: flex;
}
.formitem {
  margin-bottom: 0px;
  min-height: 36px;
  line-height: 35px;
}
.formtitle {
  // min-width: 100px;
  font-size: 14px;
  font-weight: 600;
  color: #666666;
  // width: 30%;
  text-align: right;
}
.formcontent {
  margin-left: 8px;
  font-size: 14px;
  // font-weight: 600;
  color: #606366;
}
.choice {
  cursor: pointer;
  ::v-deep .el-input__inner {
    cursor: pointer;
  }
}
